* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
  margin-left: 37px;
}
.country-info__block {
  display: inline-block;
  margin-left: 37px;
  display: flex;
  gap: 19px;
}
.country-list {
  list-style: none;
}

.country-list__text {
  font-size: 15px;
}
.country-list__item {
  display: flex;
  align-items: center;
  gap: 10px;
}
